<template>
  <div id="main-div">

    <div id="div-1">
      <span>选择客户：</span>
      <Select v-model="select_value" @on-change="optionChange" filterable style="width:200px">
        <Option v-for="item in option" :value="item"></Option>
      </Select>
    </div>

    <div id="div-2">
      <div>{{select_value}}</div>
      <table border="0" cellspacing="0" cellpadding="0">
        <tr>
          <td><span>性别：</span>{{data['性别'] |isNull}}</td>
          <td><span>年龄：</span>{{data['年龄'] |isNull}}</td>
          <td><span>开户时长：</span>{{data['开户时长分段'] |isNull}}</td>
        </tr>
        <tr>
          <td><span>是否持有存贷宝：</span>{{data['是否持有存贷宝'] |isNull}}</td>
          <td><span>理财投资风险偏好：</span>{{data['基金投资风险偏好'] |isNull}}</td>
          <td><span>基金投资风险偏好：</span>{{data['理财投资风险偏好'] |isNull}}</td>
        </tr>
      </table>
    </div>

    <div id="div-3">
      <div>推荐产品</div>
      <Table border :columns="columns" :data="columns_data_final" @on-row-dblclick="rowClick" @on-filter-change="filterChange"></Table>
    </div>

    <div id="div-page">
      <Page :current="current_page" :total="total_number_filter" :page-size="single_page_num" show-elevator @on-change="pageChange"></Page>
    </div>

    <!-- 浮框 -->
    <div id="div-box" v-show="div_box_show">
      <div>
        <table border="0">
          <tr>
            <td colspan="2"><span>产品：</span>{{box_data['产品']}}</td>
          </tr>
          <tr>
            <td rowspan="6"><span>推荐总分：</span>{{box_data['推荐总分']}}</td>
          </tr>
          <tr><td><span>风险匹配：</span>{{box_data['风险匹配']}}</td></tr>
          <tr><td><span>定存分值：</span>{{box_data['定存分值']}}</td></tr>
          <tr><td><span>关注分值：</span>{{box_data['关注分值']}}</td></tr>
          <tr><td><span>新户匹配：</span>{{box_data['新户匹配']}}</td></tr>
          <tr><td><span>产品冲突：</span>{{box_data['产品冲突']}}</td></tr>
        </table>
      </div>
      <Button @click="confirm" type="primary">确认</Button>
    </div>

  </div>
</template>

<script>
import $ from 'jquery'
export default {
  name: 'HelloWorld',
  data () {
    return {
      current_page: 1,
      option: [],
      box_data: {
        "产品":"",
        "推荐总分":"",
        "风险匹配":"",
        "定存分值":"",
        "关注分值":"",
        "新户匹配":"",
        "产品冲突":""
      },
      div_box_show: false,
      single_page_num: 10,
      select_value: '',
      user_info: {

      },
      data: [], //所有获取到的数据
      columns_data: [], //表格获取到的所有数据
      columns_data_filter: [],  //表格筛选后的数据
      columns_data_final: [], //表格筛选后&分页后的最终渲染数据
      total_number_filter: 0,
      selected_filter: {  //展示的
        "产品分类": [],
        "募集结束": [],
        "往来时长": [],
        "风险等级": [],
        "推荐总分": []
      },
      selected_filter_select: { //筛选选中的
        "产品分类": [],
        "募集结束": [],
        "往来时长": [],
        "风险等级": [],
        "推荐总分": []
      },
      columns: [
        {
          title: '推荐产品',
          key: '产品'
        }, {
          title: '产品分类',
          key: '产品分类',
          filters: {},
          filterRemote: this.filterClick
        }, {
          title: '募集结束',
          key: '募集结束',
          filters: {},
          filterRemote: this.filterClick
        }, {
          title: '往来时长',
          key: '往来时长',
          filters: {},
          filterRemote: this.filterClick
        }, {
          title: '风险等级',
          key: '风险等级',
          filters: {},
          filterRemote: this.filterClick
        }, {
          title: '推荐总分',
          key: '推荐总分',
          filters: {},
          filterRemote: this.filterClick
        }, {
          title: '推荐产品分类排名',
          key: '推荐产品分类排名'
        }, {
          title: '推荐购买概率',
          key: '推荐购买概率'
        }
      ]
    }
  },
  filters: {
    isNull(value) {
      if (!value || value == '' || value == null) {
        return '无'
      }
      return value
    }
  },
  mounted() {
    this.createOption(249)
    this.select_value = '客户001'
  },
  methods: {
    /** 选择 */
    optionChange(value) {
      this.getData(value)
    },
    /** 创建选项 */
    createOption(num) {
      for (var i = 1; i <= num; i ++) {
        var uid = '客户'
        if (i < 10) {
          uid += '00' + i
        } else if (i < 100) {
          uid += '0' + i
        } else {
          uid += i
        }
        this.option.push(uid)
      }
    },
    /** 通过用户名称获取数据 */
    getData(uid) {
      var url = "static/json/" + uid + ".json"

      $.getJSON(url, (data) => {
        this.columns_data = data['推荐产品']
        this.data = data
        this.filterClick()
        this.getFilterOption(this.columns_data_filter)
      })
    },
    /** 判断数组是否存在某值 */
    isInArray(arr,value) {
        for(var i = 0; i < arr.length; i++){
            if(value === arr[i].label){
                return true;
            }
        }
        return false;
    },
    /** 获取所有筛选项 */
    getFilterOption(data) {
      var _this = this
      this.selected_filter = {
        "产品分类": [],
        "募集结束": [],
        "往来时长": [],
        "风险等级": [],
        "推荐总分": []
      }
      //distinct option
      for (var i in data) {
        for (var j in _this.selected_filter) {
          if (data[i][j]!=null && !_this.isInArray(_this.selected_filter[j], data[i][j])) {
            _this.selected_filter[j].push({
              label: data[i][j],
              value: data[i][j]
            })
          }
        }
      }
      //更换filter内容
      for(var i in _this.columns) {
        for (var j in _this.selected_filter) {
          if (_this.columns[i].title == j) {
            _this.columns[i].filters = _this.selected_filter[j]
          }
        }
      }
    },
    /** 页码切换 */
    pageChange(num) {
      this.current_page = num
      var start = (num - 1) * 10
      var end = num * 10 - 1

      this.columns_data_final = this.columns_data_filter.slice(start, end)
    },
    /** 点击行 */
    rowClick(data, index) {
      this.div_box_show = true
      this.box_data = {
        "产品":"",
        "推荐总分":"",
        "风险匹配":"",
        "定存分值":"",
        "关注分值":"",
        "新户匹配":"",
        "产品冲突":""
      }
      for (var i in this.box_data) {
        this.box_data[i] = data[i]
        if(data[i] == undefined) {
          this.box_data[i] = '无'
        }
      }
    },
    /** 浮框 */
    confirm() {
      this.div_box_show = false
    },
    /** 判断数组是否存在某值 */
    isInArrayFun(arr,value) {
        for(var i = 0; i < arr.length; i++){
            if(value === arr[i]){
                return true;
            }
        }
        return false;
    },
    /** 远程筛选 */
    filterClick(value, row) {
      //TODO: 
      var _this = this
      this.columns_data_filter = []
      if (!value) {
        this.columns_data_filter = this.columns_data
      } else {

        this.selected_filter_select[row] = value
        var s = this.selected_filter_select
        var c = this.columns_data

        for (var i in c) {

          var flag = true
          for (var j in s) {
            if (s[j].length > 0 && !_this.isInArrayFun(s[j], c[i][j])) {
              flag = false
            }

          }
          if (flag) {
            _this.columns_data_filter.push(c[i])
          }

        }

      }
      
      // this.getFilterOption(this.columns_data_filter)

      this.total_number_filter = this.columns_data_filter.length
      this.pageChange(1)
    }, 
    /** 筛选发生改变触发、暂时不用 */
    filterChange(data) {
      // console.log(111)
      // this.current_page = 1
      // console.log(222)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
#main-div {
  width: 80%;
  margin-left: 10%;
  text-align: left;
  font-size: 20px;
}

#div-1 {
  margin-top: 30px;
  span {

  }
}

#div-2 {
  overflow-x: auto;
  font-size: 16px;
  margin-top: 20px;
  width: 100%;

  div {
    text-align: right;
    margin-bottom: 10px;
  }

  table {
    padding-bottom: 30px;
    span {
      float: none;
    }
    td {
      padding: 30px 40px 0 40px;
      span {
        color: #495060;
        font-weight: 700;
        margin-right: 20px;
      }
    }
    width: 100%;
    min-width: 1000px;
    border:1px solid #e9eaec;
  }
}

#div-3 {
  div {
    font-size: 16px;
    text-align: right;
    margin-bottom: 10px;
  }
  margin-top: 50px;
  height: 550px;
  overflow-y: auto;
}

#div-page {
  margin-top:20px;
  margin-bottom: 80px;
  text-align:center;
}

#div-box {
  display: inline-block;
  position: fixed;
  top: calc(50% - 175px);
  left: calc(50% - 200px);
  width: 400px;
  height: 350px;
  border: 1px solid #1c2438;
  background-color: rgb(255, 255, 255);
  overflow-y: auto;
  div {
    height: 300px;
    overflow-y: auto;
    table {
      width: 100%;
      min-height: 100%;
      td {
        padding-left: 20px;
        padding-top: 10px;
      }
    }
  }
  button {
    position: absolute;
    bottom: 20px;
    right: 20px;
  }
}

</style>



